<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item>
            {{ $route.query.id ? '' : '' }}
          </el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-form
        ref="form"
        :model="article"
        label-width="60px"
        :rules="rules"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="article.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-tiptap
            v-model="article.content"
            :extensions="extensions"
            lang="zh"
            placeholder="..."
            :height="400"
          />
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <div
            style="display:flex;"
            v-if="article.cover.type > 0"
          >
            <!--  v-model 
                 v-model="article"
                 
                 1.  value  :value="article"
                 2.  input @input="article=[]"
            -->
            <upload-cover
              v-for="(item, index) in article.cover.type" :key="item"
              v-model="article.cover.images[index]"
            ></upload-cover>
          </div>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <el-select v-model="article.channel_id" placeholder="">
            <el-option
              v-for="(item, index) in channels" :key='index'
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onPublish(false)">
            {{ $route.query.id ? '' : '' }}
          </el-button>
          <el-button @click="onPublish(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {
  getArticleChannel,
  publishArticle,
  getArticleInfo,
  updateArticle
} from 'api/article.js'

import { uploadImage } from 'api/image.js'

import {
  ElementTiptap,
  //  extensions
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Italic,
  Strike,
  Underline,
  Link,
  Image,
  Iframe,
  CodeBlock,
  Blockquote,
  ListItem,
  BulletList, // ( ListItem )
  OrderedList, // ( ListItem)
  TodoItem,
  TodoList, // ( TodoItem )
  TextAlign,
  LineHeight,
  HorizontalRule,
  HardBreak,
  TrailingNode,
  History,
  Table, // ( TableHeader, TableCell, TableRow )
  TableHeader,
  TableCell,
  TableRow,
  FormatClear,
  TextColor,
  TextHighlight,
  Preview,
  Print,
  Fullscreen,
  FontType,
  FontSize
} from 'element-tiptap'

import 'element-tiptap/lib/index.css'

import UploadCover from './components/UploadCover.vue'

export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  data() {
    return {
      article: {
        title: '', // 
        content: '', // 
        cover: { // 
          type: 0, // -1: 0: 1: 3: 
          images: []
        },
        channel_id: null // 
      },
      channels: [], // 
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }), // 
        new FontType(),
        new FontSize(),
        new Bold({ bubble: true }), // 
        new Underline({ bubble: true, menubar: false }), // 
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new Link(),
        new Image({
          async uploadRequest(file) {
            const fd = new FormData() //  FormData 
            fd.append('image', file) // Body  image  file 
            const res = await uploadImage(fd)
            return res.data.data.url // 
          }
        }),
        new Iframe(),
        new CodeBlock(),
        new Blockquote(),
        new TodoItem(),
        new TodoList(),
        new TextAlign(),
        new LineHeight(),
        new HorizontalRule(),
        new HardBreak(),
        new TrailingNode(),
        new History(),
        new Table(),
        new TableHeader(),
        new TableCell(),
        new TableRow(),
        new FormatClear(),
        new TextColor(),
        new TextHighlight(),
        new Preview(),
        new Print(),
        new Fullscreen()
      ],
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5-30 ', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator(rule, value, callback) {
              if (value === '<p></p>') {
                callback(new Error(''))
              }
              callback()
            },
            trigger: 'blur'
          }
        ],
        channel_id: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.loadArticleChannel()
    if (this.$route.query.id) { //  id
      this.loadArticleInfo()
    }
  },
  methods: {
    async loadArticleChannel() {
      try {
        const res = await getArticleChannel()
        this.channels = res.data.data.channels
      } catch (ex) {
        console.log(ex)
      }
    },
    onPublish(draft) {
      const self = this
      this.$refs.form.validate(async function(valid) {
        if (!valid) return false
        if (self.$route.query.id) { //  id
          try {
            await updateArticle(self.$route.query.id, self.article, draft)
            self.$msgSuccess(`${draft ? '' : ''}`)
            self.$router.push({ name: 'article' }) // 
          } catch (ex) {
            self.$msgError(`${draft ? '' : ''}`)
            console.log(ex)
          }
        } else { // 
          try {
            await publishArticle(self.article, draft)
            self.$msgSuccess(`${draft ? '' : ''}`)
            self.$router.push({ name: 'article' }) // 
          } catch (ex) {
            self.$msgError(`${draft ? '' : ''}`)
            console.log(ex)
          }
        }
      })
    },
    async loadArticleInfo() {
      try {
        const res = await getArticleInfo(this.$route.query.id)
        this.article = res.data.data
      } catch (ex) {
        console.log(ex)
      }
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
